import util from '@/common/util.js'
import graceChecker from '@/common/graceChecker.js'
import md5 from '@/common/md5.js'
import {mapState} from "vuex"
export default {
	data() {
		return {
			modalName: null,
			userId: "",
			password: ""
		}
	},
	computed:{
		...mapState({
			isLogin:state=>state.isLogin,
			isSDKReady:state=>state.isSDKReady
		})
	},
	onShow() {
		this.$store.commit('reset')
	},
	methods: {
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		login(){
			let _this = this
			var registerRule = [{
					name: 'userId',
					checkType: 'notnull',
					checkRule: '',
					errorMsg: '请输入账号'
				},
				{
					name: 'password',
					checkType: 'notnull',
					errorMsg: '请输入登录密码'
				},
				{
					name: 'password',
					checkType: 'string',
					checkRule: '6,32',
					errorMsg: '密码最为 6-32 个字符'
				}
			];
			
			var checkRes = graceChecker.check(_this, registerRule);
			if (checkRes) {
				uni.request({
					url: this.Server_IP + 'login', //仅为示例，并非真实接口地址。
					data: {
						userId: this.userId,
						password: md5(this.password)
					},
					header: {
						'custom-header': 'login' //自定义请求头信息
					},
					sslVerify: false,
					method:"POST",
					dataType:"json",
					success: (res) => {
						console.log(res.data);
						if(res.data.info.code == '0'){
							uni.setStorageSync('userId', res.data.data.userId);
							uni.setStorageSync('isLogin', true);
							console.log(res.data.data.userId)
							console.log("成功")
							uni.request({
								url: this.Server_IP + 'userInformation', //仅为示例，并非真实接口地址。
								data: {
									userId: this.userId
								},
								header: {
									'custom-header': 'userInformation' //自定义请求头信息
								},
								method:"POST",
								dataType:"json",
								success: (res) => {
									console.log(res.data);
									if(res.data.info.code == '0'){
										uni.setStorageSync('userName', res.data.data.userName);
										uni.setStorageSync('mail', res.data.data.mail);
										uni.setStorageSync('telephone', res.data.data.telephone);
										uni.setStorageSync('sex', res.data.data.sex);
										uni.setStorageSync('birthday', res.data.data.birthday);
										uni.setStorageSync('synopsis', res.data.data.synopsis);
										console.log(2222222222222222222222222222222222222)
										console.warn(this.userId)
										console.warn(res.data.data.userSig)
										console.log(2222222222222222222222222222222222222)
										let promise = this.tim.login({
											userID: this.userId,
											userSig: res.data.data.userSig
										});
										promise.then((rest) => {
											//登录成功后 更新登录状态
											this.$store.commit("toggleIsLogin", true);
											//tim 返回的用户信息
											uni.setStorageSync('userTIMInfo', JSON.stringify(rest.data))
											console.log(11111111111111111111111111111111111111)
											console.log(rest)
											console.log(JSON.stringify(rest.data))
											console.log(11111111111111111111111111111111111111)
										}).catch((err) => {
											console.warn(this.userId)
											console.warn(res.data.data.userSig)
											console.warn('login error:', err); // 登录失败的相关信息
										});
										console.log("获取用户信息成功")
									}else{
										console.log("获取用户信息失败")
									}
								},
								fail() {
									console.log("登录信息请求失败")
								}
							});
							
							uni.request({
								url: this.Server_IP + 'queryField', //仅为示例，并非真实接口地址。
								data: {
									userId: this.userId
								},
								header: {
									'custom-header': 'queryField' //自定义请求头信息
								},
								method:"POST",
								dataType:"json",
								success: (res) => {
									console.log(res.data);
									if(res.data.info.code == '0'){
										uni.setStorageSync('field', JSON.parse(res.data.data.field));
										// console.log(res.data.data.field)
										// var field = JSON.parse(res.data.data.field)
										// console.log(field.userId);
										// console.log(field)
										console.log("获取用户喜好成功")
										uni.request({
										    url: this.Server_IP + 'followsay', //仅为示例，并非真实接口地址。
										    data: {
												userId: this.userId,
												start: 0,
												pagesize: 3
										    },
										    header: {
										        'custom-header': 'followsay' //自定义请求头信息
										    },
											method:"POST",
											dataType:"json",
										    success: (res) => {
										        console.log("获取关注用户微博第一页成功");
												if(res.data.info.code == '0'){
													if(res.data.data.sayList == ""){
														var sayList = []
													}else{
														console.log(res)
														console.log(res.data.data.sayList)
														if(res.data.data.sayList == ""){
															sayList = ""
														}else{
															var sayList = JSON.parse(res.data.data.sayList)
															for(var index = 0; index < sayList.length; index++){
																var url = sayList[index].picUrl
																sayList[index].picUrl = JSON.parse(url)
															}																								
														}
													}
													uni.setStorageSync('sayList', sayList);
													uni.setStorageSync('Fstart', 3);
													uni.switchTab({
														url: '/pages/index/index'
													});
												}else{
												}
										    },
										});
									}else{
										console.log("获取用户喜好失败")
									}
								},
								fail() {
									console.log("登录信息请求失败")
								}
							});
						}else{
							uni.showToast({
								icon: 'none',
								title: res.data.info.message
							});
						}
					},
				});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
				_this.isFocus = true;
				return;
			}
		},
		register(){
			uni.navigateTo({
			    url: './register'
			});
		},
		forgetPassword(){
			console.log("forgetPassword")
			uni.navigateTo({
			    url: './forget_password'
			});
		},
		mailLogin(){
			console.log("mailLogin")
			uni.navigateTo({
			    url: './mail_login'
			});
		}
	}
}
